@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
::-webkit-scrollbar{display:none}
*{
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}
html,body,.App,#root{
  width: 100%;
  height: 100vh;
}
/* Style Guide
    1. section-body
        1.1 bodyHeader
        1.2 banner
        1.3 categories
        1.4 types
        1.5 foods
    2. section-order
        2.1 orderHeader
        2.2 orderBody
        2.3 orderProducts
        2.4 orderFooter 
        2.5 checkoutPage 
        2.5 myAccount
        
*/

/*--------- 1. section-body ---------*/
.main{
    width: 100%;
    height: 100vh;
    display: flex;
}

.section-body{
    width: 100%;
    height: 100vh;
    background-color: white;
}
.section-body-container{
    width: 64%;
    max-width: 1000px;
    margin-left: 100px;
}


/*--------- 1.1 bodyHeader ---------*/


.bodyHeader{
    width: 100%;
    height: 50px;
    margin-top: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.groupDivHeader{
    width: 250px;
    height: 100%;
    display: flex;
    align-items: center;
}

.two-lines{
    width: 40px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.line{
    width: 80%;
    height: 2px;
    background-color: black;
    margin: 3px;
}
.headerTitle{
    font-size: 23px;
    font-weight: 600;
    margin: 0px 30px 0px 30px;
}
.input{
    width: 670px;
    height: 100%;
    display: flex;
    align-items: center;
    border-radius: 16px;
    padding-left: 20px;
    background-color: #f8f8f8;
    overflow: hidden;
}
.inputSearch{
    width: 90%;
    margin-left: 5px;
    padding: 10px;
    background-color: #f8f8f8;
    border: none;
    outline: none;
    font-size: 15px;
    pointer-events: none;
}
.more{
    width: 50px;
    height: 30px;
    background-image: url(/chukwudi-restaurant/static/media/more.ffc7b8d9.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin-left: 10px;
    display: none;
}


/*--------- 1.2 banner ---------*/

.banner{
    width: 100%;
    height: 190px;
    background-color: #fff7eb;
    margin-top: 80px;
    border-radius: 16px;
    display: flex;
}
.banner-img{
    width: 463px;
    height: 230px;
    bottom: 41px;
    margin-left: 50px;
    position: relative;
    background-image: url(/chukwudi-restaurant/static/media/headerimage.cdead91b.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
.banner-text{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}
.banner-text h1{
    margin-right: 50px;
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: bold;
    color: #ff6223;
}
.banner-text p{
    margin-right: 100px;
    font-size: 15px;
    color: #9c9c9c;
}
.hat{
    margin-left: 20px;
    position: relative;
    top: 3px;
}
.hatIcon{
    width: 29px;
}
.banner-more{
    width: 30%;
    height: 100%;
    display: flex;
    align-items: flex-end;
}
.banner-more a{
    text-decoration: none;
    margin-bottom: 25px;
    font-weight: bold;
    color: #ff6223;
}

/*--------- 1.3 categories ---------*/

.categories{
    width: 100%;
    height: 50px;
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.categories h1{
    font-size: 30px;
    font-weight: bold;
}
.burger{
    position: relative;
    margin-left: 8px;
}
.delivery-time{
    width: 180px;
    height: 40px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 50px;
    color: white;
    background-color: #ff6223;
    cursor: pointer;
}
.clock {
    width: 20px;
}
.delivery-time p{
    font-size: 13px;
    height: 25%;
}
.time{
    font-weight: bold;
}
.shortRightArrow{
    width: 12px;
    transform:          rotate(90deg);
    -ms-transform:      rotate(90deg);
    -moz-transform:     rotate(90deg);
    -webkit-transform:  rotate(90deg);
    -o-transform:       rotate(90deg);
}


/*--------- 1.4 types ---------*/
.types{
    width: 100%;
    height: 200px;
    display: flex;
}
.group-type{
    width: 90%;
    height: 100%;
    display: flex;
    overflow: scroll;
}
.type{
    width: 90px;
    height: 150px;
    margin-top: 20px;
    margin-right: 25px;
    padding: 5px;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 45px;
    background-color: white;
    cursor: pointer;
    box-shadow: 1px 1px 21px -15px black;
}

.type span{
    font-weight: bold;
    font-size: 14px;
}
.circleType{
    width: 70px;
    height: 70px;
    display: flex;
    margin-top: 10px;
    margin-bottom: 20px;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 100%;
    border: 1px solid #d6d6d6;
}
.arrowNextDiv{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 10%;
}
.arrowNextIcon{
    width: 41px;
    height: 38px;
    padding: 10px;
    border-radius: 15px;
    z-index: 0;
    background-color: #ececec;
}

/*--------- 1.5 foods ---------*/

.foods{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    margin-top: 70px;
    margin-bottom: 20px;
}

.food{
    width: 19vw;
    height: auto;
    margin-bottom: 50px;
    margin-right: 24px;
    cursor: pointer;
}
.foodImg{
    width: 100%;
    height: 160px;
    border-radius: 15px;
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: flex-end;
}
.travelTime{
    width: 100px;
    padding: 10px;
    text-align: center;
    border-top-right-radius: 15px;
    background-color: white;
}
.travelTime p{
    font-size: 14px;
    font-weight: bold;
}
.foodDescription{
    margin-top: 10px;
}
.foodDescription h4{
    font-weight: 500;
    font-size: 1.3vw;
    margin-bottom: 10px;
}
.star{
    width: 1vw;
    cursor: pointer;
}
.puntation{
    display: flex;
    align-items: center;
}
.puntation span{
    margin-left: 1vw;
    font-size: 1vw;
}
.puntation span:nth-child(2){
    font-weight: 500;
}


/*--------- 2. section-order ---------*/
.showSectionOrder{
    visibility: visible !important;
    width: 100% !important;
}
.AppOverflow{
    overflow: hidden;
}

.section-order{
    width: 23vw;
    height: 100%;
    position: fixed;
    right: 1px;
    display: flex;
    flex-direction: column;
    background-color: #fcfcfa;
    border-left: 1px solid #e6e6e6;
    transition: width 0.3s;
    overflow: scroll;
}
.section-order-container{
    width: 80%;
    height: 100%;
    margin: 0 auto;
}

/*--------- 2.1 orderHeader ---------*/

.orderHeader{
    width: 100%;
    height: 50px;
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.backArrow{
    display: none;
    transform: rotate(180deg);
}
.otherButtons{
    display: flex;
    justify-content: center;
    align-items: center;
}
.orderLogin{
    margin-right: 12px !important;
    font-size: 16px;
}
.user{
    width: 30px;
    border-radius: 100%;
    margin-right: 30px;
    margin-left: 15px;
    cursor: pointer;
}
.logged{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.quantityProducts{
    width: 40px;
    height: 40px;
    border-radius: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffd644;
}
.quantityProducts span{
    font-weight: bold;
}

/*--------- 2.2 orderBody ---------*/

.orderBody{
    width: 100%;
    height: 70%;
}
.orderTitle{
    width: 130px;
    margin-top: 60px;
}
.orderTitle h2{
    font-size: 22px;
    font-weight: bold;
}
.orderInformation{
    width: 93%;
    height: 114px;
    margin-top: 20px;
    border-radius: 20px;
    padding: 10px;
    font-size: 12px;
    background-color: #5539a3;
}
.info1{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    color: white;
}
.info1 span:nth-child(2){
    color:#ffd644;
    font-weight: 500;
}
.info2{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:20px 10px 10px 10px;
}
.iconClockInfo2{
    width: 35px;
    height: 35px;
    background-color: #66489c;
    padding: 10px;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.yellowClock{
    width: 20px;
}
.info2-min{
    color: white;
}
.info2-chooseTime{
    margin-right: 18px;
    color:#ffd644;
}

/*--------- 2.3 orderProducts ---------*/

.orderProducts{
    width: 100%;
    height: 200px;
    margin-top: 20px;
    overflow: scroll;
    border-bottom: 1px solid #cacaca;
}
.product{
    width: 100%;
    height: 35px;
    margin-bottom: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 13px;
}
.productImg{
    width: 70px;
    height: 100%;
    border-radius: 10px;
    background-color: teal;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.nameQuantity{
    font-weight: 500;
}
.totalPrice{
    color: #c5c5c5;
}
.productImgDelivery{
    width: 70px;
    height: 100%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    background-color: #fdf2d2;
}
.scrollDown{
    text-align: center;
    margin-top: 20px;
}
/*--------- 2.4 orderFooter ---------*/

.orderFooter{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 30px;
}
.total{
    width: 90%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 20px;
}
.total h4{
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 10px;
}
.total h4:nth-child(2){
    font-size: 23px;
    font-weight: bold;
    margin-right: 20px;
}
.quantity{
    width: 90%;
    display: flex;
    margin-bottom: 10px;
}
.subtitlePerson{
    font-size: 15px;
    margin-bottom: 10px;
}
.persons{
    width: 50%;
    display: flex;
    flex-direction: column;
}
.quantityPersons{
    width: 100px;
    height: 30px;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fcfcfa;
    border: 1px solid #e6e6e6;
}
.quantityPersons > *{
    flex: 1 1;
    text-align: center;
    font-size: 20px;
    font-weight: 500;
}
.less, .sum{
    cursor: pointer;
}
.checkout{
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    background-color: #ffd644;
    cursor: pointer;
}
.checkout span{
    font-size: 13px;
    font-weight: bold;
    margin: 15px;
}
.rightArrow{
    width: 15px;
}
/*---------- 2.5 checkoutPage -------*/

.ant-drawer-title{
    text-align: center;
    font-size: 20px;
}
.checkoutPage{
    width: 80%;
    height: 100%;
    margin: 0 auto;
}
.buy {
    width: 150px;
    height: auto;
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    margin-bottom: 20px;
}
.buy-total{
    font-size: 20px;
    margin-bottom: 10px;
}
.buyButton{
    background-color: #ffd644 !important;
    outline: none;
    border: none;
}
.buyButton:hover{
    background-color:#ffc700 !important;
}
.ant-pagination-item-active {
    border-color: #ffc700 !important;;
}
.ant-pagination-item-active a {
    color: #ffc700 !important;
}
.checkoutButton{
    margin-top:15px;
    margin-bottom: 20px;
}

/*---------- 2.5 myAccount -------*/
.myAccount{
    text-align: center;
}
.profilePhoto{
    width: 150px;
    border-radius: 100%;
    margin-bottom: 10px;
    border: 7px solid white;
}
.ant-modal-header {
    border-bottom: 1px solid #c8c8c8;
}
.ant-modal-footer{
    border-top: none;
    background-color: #f0f0f0;
}
.ant-modal-body {
    background-color: #f0f0f0;
}
@media only screen and (min-width:1800px){
    .section-body-container{
        margin: 0 auto;
    }


    /*------- foods --------*/
    .food {
        max-width: 309px;
    }
    .puntation span {
        font-size: 16px;
    }
    .star {
        width: 20px;
    }
    .foodDescription h4 {
        font-size: 25px;
    }

}

@media only screen and (max-width:1440px){

    /*------- section-body-container --------*/
    .section-body-container {
        margin-left: 80px;
    }



    /*------- banner --------*/
    .banner-text h1 {
        font-size: 2vw;
        margin-right: 2vw;
    }
    .banner-text p {
        margin-right: 5vw;
        font-size: 1vw;
    }
    .hat {
        margin-left: 1vw;
    }
    .hatIcon {
        width: 2vw;
    }
    .banner-more a {
        font-size: 1vw;
    }
    .banner-img {
        margin-left: 3vw;
        bottom: 2.6vw;
    }

    /*------- categories --------*/
    .categories h1 {
        font-size: 2.4vw;
    }
    .delivery-time {
        width: 15vw;
        height: 3vw;
    }
    .clock {
        width: 2vw;
    }
    .shortRightArrow {
        width: 1vw;
    }

    /*------- section-order --------*/
    .section-order{
        width: 22vw;
    }
    .info1, .info2 {
        font-size: 1vw;
    }
    .iconClockInfo2 {
        width: 1.5vw;
        height: 1.5vw;
    }
    .yellowClock {
        width: 1.5vw;
    }
    .product {
        font-size: 0.9vw;
    }
    .productImg,.productImgDelivery {
        width: 6vw;
        margin-right: 1vw;
    }
    .subtitlePerson {
        font-size: 1vw;
    }
    .quantityPersons {
        width: 7vw;
    }
    .checkout span {
        font-size: 1.1vw;
        margin: 0.7vw;
    }
    .rightArrow {
        width: 1.5vw;
    }

    /*------- foods --------*/
    .food {
        margin-right: 2vw;
    }

}
@media only screen and (max-width:1024px){

    /*------- .section-order --------*/
    .orderInformation {
        height: 90px;
    }
    .quantityProducts {
        width: 38px;
        height: 30px;
    }
    .orderLogin{
        font-size: 12px;
    }
    
}

@media only screen and (max-width:900px){
    /*------- .section-order --------*/
    .section-order{
        overflow: scroll;
    }  
    .orderLogin{
        margin: 0px !important;
    }
}


@media only screen and (max-width:800px){

    /*------- .section-order --------*/
    .section-order{
        z-index: 100;
        visibility: hidden;
        overflow: scroll;
    }  
    .backArrow{
        display: block;
    }
    .orderBody {
        height: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .orderTitle {
        width: 135px;
    } 
    
    .orderInformation {
        width: 40%;
    }
    .more{
        display: block;
    }
    .section-body-container {
        width: 90%;
        margin: 0 auto;
    }
    .quantityPersons {
        width: 130px;
    }
    .subtitlePerson {
        font-size: 20px;
    }
    .product {
        height: 50px;
        font-size: 13px;
        margin-bottom: 8px;
    }
    .productImg, .productImgDelivery {
        width: 70px;
    }
    .info1, .info2 {
        font-size: 13px;
    }
    .checkout span {
        font-size: 15px;
    }

    /*------- banner--------*/
    .banner {
        margin-top: 55px;
    }
    .banner-img {
        bottom: 4vw;
    }
    .banner-text {
        align-items: center;
        text-align: center;
    }
    .hatIcon {
        width: 3vw;
    }
    .banner-text h1 {
        font-size: 3vw;
        margin-right: 5vw;
    }
    .banner-text p {
        margin-right: 8vw;
        font-size: 1.5vw;
    }
    .banner-more a {
        font-size: 1.5vw;
    }

    /*------- categories--------*/
    .delivery-time {
        width: 180px;
        height: 40px;
    }
    .categories h1 {
        font-size: 3vw;
    }
    .clock {
        width: 2.4vw;
    }
    .shortRightArrow {
        width: 1.5vw;
    }


    /*------- foods --------*/
    .food {
        width: 28vw;
    }
    .foodDescription h4 {
        font-size: 2.3vw;
    }
    .puntation span {
        font-size: 2vw;
    }
    .star {
        width: 2vw;
    }


}

@media only screen and (max-width:600px){

    /*--------- 1.1 bodyHeader ---------*/
    .bodyHeader {
        height: 40px;
    }
    .headerTitle {
        font-size: 19px;
    }
    .input {
        width: 380px;
    }
    .inputSearch {
        width: 77%;
    }

    /*------- banner--------*/
    .banner {
        width: 100%;
        height: 172px;
    }
    .hat {
        display: none;
    }
    .banner-img {
        bottom: 6.6vw;
    }
    .banner-text p {
        margin-right: 5vw;
    }
    .banner-more {
        width: 35%;
    }
    .banner-text h1 {
        font-size: 2.9vw;
    }

    /*------- .section-order --------*/
    .orderInformation {
        width: 50%;
        height: 103px;
    }
    .iconClockInfo2 {
        width: 40px;
        height: 30px;
    }
    .yellowClock {
        width: 18px;
    }

}


@media only screen and (max-width:500px){
    /*------- banner--------*/
    .banner-text p {
        font-size: 2vw;
    }
    .banner-more {
        width: 60%;
    }
    .banner-more a {
        font-size: 1.9vw;
    }
    .banner-img {
        bottom: 4.6vw;
    }
    .banner-text {
        text-align: center;
    }

    /*------- categories--------*/
    .clock {
        width: 3.4vw;
    }
    .shortRightArrow {
        width: 2.5vw;
    }
    .categories h1 {
        font-size: 4vw;
    }
    
    /*------- foods --------*/

    .food {
        width: 100%;
    }
    .foodDescription h4 {
        font-size: 6vw;
    }
    .star {
        width: 3vw;
    }
    .puntation span {
        font-size: 3vw;
    }
    .foodImg {
        height: 180px;
    }

    /*------- .section-order --------*/
    .orderInformation {
        width: 70%;
    }

}

@media only screen and (max-width:400px){
    .banner-img {
        width: 100px;
    }

    /*--------- 1.1 bodyHeader ---------*/
    .two-lines {
        width: 30px;
    }
    .headerTitle {
        font-size: 18px;
        margin: 0px 30px 0px 12px;
    }   
    .input {
        width: 236px;
    } 

    /*------- banner--------*/
    .banner {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .banner-img{
        margin: 0;
    }
    .banner-more {
        align-items: center;
        justify-content: center;
    }
    .banner-text h1 {
        font-size: 4.9vw;
    }
    .banner-text p {
        font-size: 3vw;
    }
    .banner-more a {
        font-size: 3vw;
    }

    /*------- categories--------*/
    .categories h1 {
        font-size: 5vw;
    }
    .burger{
        display: none;
    }
    .delivery-time {
        width: 150px;
        height: 35px;
    }
    .clock {
        width: 4.4vw;
    }
    .delivery-time p {
        font-size: 12px;
    }
    .shortRightArrow {
        width: 3.5vw;
    }
    .arrowNext {
        width: 15%;
    }

    /*------- foods --------*/
    .puntation span {
        font-size: 4vw;
    }
    .star {
        width: 4.4vw;
    }

    /*------- section-order --------*/
    .orderInformation {
        width: 80%;
    }
    .quantityPersons {
        width: 110px;
    }

}

@media only screen and (max-width:350px){

    /*--------- 1.1 bodyHeader ---------*/
    .two-lines {
        width: 25px;
    }
    .headerTitle {
        margin: 0px 9px 0px 12px;
    }
    .input {
        width: 184px;
    }
    .inputSearch {
        font-size: 13px;
    }

